.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #FF7417;
  border-radius: 4px;
  background: #FF7417;
  color: white;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background:#FF7417;
  border-color:#FF7417;
}

.button--inverse {
  background: transparent;
  color: #FF7417;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background:#FF7417;
}

.button--danger {
  background: #FF7417;
